export default (sequelize, Sequelize) => {
  const PriceItem = sequelize.define("price_item", {
    termType: {
      type: Sequelize.STRING,
    },
    // typeCourseId: {
    //   type: Sequelize.INTEGER,
    // },
    pricePerUnit: {
      type: Sequelize.INTEGER,
    },
    summaryPrice: {
      type: Sequelize.INTEGER,
    },
  });
  return PriceItem;
};
